<template>
  <div class="bg">
    <div class="title">{{$route.query.status == 'edit' ? '项目修改': '项目发布'}}</div>
    <br />
    <br />
    <div class="item_box">
      <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="180">
        <Steps :current="active" style="margin: 0px auto; width: 800px;margin-bottom:50px;">
          <Step title="基本信息" content="请填写项目基本信息"></Step>
          <Step title="融资信息" content="融资轮次/金额等信息"></Step>
          <Step title="团队信息" content="团队成员"></Step>
          <Step title="项目联系人" content="项目联系人"></Step>
        </Steps>
        <div v-show="active==0">
          <form-item label="项目类型" prop="type" @submit.native.prevent>
            <RadioGroup v-model="formValidate.type" size="large" style="margin-top: -5px;">
              <Radio
                label="1"
                :disabled="userAuth.auditingType == 3 || $route.query.status == 'edit'"
              >个人项目</Radio>
              <Radio
                label="2"
                :disabled="userAuth.auditingType != 2 || $route.query.status == 'edit'"
              >企业项目</Radio>
              <Radio
                label="3"
                :disabled="userAuth.auditingType != 3 || $route.query.status == 'edit'"
              >政府项目</Radio>
            </RadioGroup>
            <br />
            <router-link
              v-if="userAuth.auditingType == 1"
              to="/user/certify-index"
              style="color:#2868d1;"
            >*当前为个人认证,想发布企业/政府项目,请点击前往升级认证</router-link>
            <router-link
              v-else-if="userAuth.auditingType == 2"
              to="/user/certify-index"
              style="color:#2868d1;"
            >*当前为企业认证,政府项目,请点击前往升级认证</router-link>
            <span
              v-else="userAuth.auditingType == 3"
              to="/user/certify-index"
              style="color:#2868d1;"
            >*当前为政府认证,无法代表个人/企业发布项目</span>
          </form-item>
          <form-item
            label="选择企业"
            prop="companyId"
            @submit.native.prevent
            v-if="formValidate.type == '2'"
            :disabled="$route.query.status == 'edit'"
          >
            <i-Select v-model="formValidate.companyId">
              <i-Option
                v-for="item in userAuth.company"
                :key="item.id"
                :value="item.id"
              >{{item.companyName}}</i-Option>
            </i-Select>
          </form-item>
          <form-item label="项目名称" prop="name" @submit.native.prevent>
            <Input
              v-model="formValidate.name"
              placeholder="项目名称"
              :disabled="$route.query.status == 'edit'"
            />
          </form-item>
          <form-item label="项目介绍" prop="introduce" @submit.native.prevent>
            <Input v-model="formValidate.introduce" type="textarea" :rows="4" placeholder="项目介绍" />
          </form-item>
          <form-item label="所属领域" prop="territory" @submit.native.prevent>
            <i-Select
              v-model="formValidate.territory"
              @on-change="fieldChange"
              :disabled="$route.query.status == 'edit'"
            >
              <i-Option v-for="item in data.realms" :key="item.id" :value="item.id">{{item.name}}</i-Option>
            </i-Select>
          </form-item>
          <form-item label="项目标签" prop="label" @submit.native.prevent>
            <i-Select
              multiple
              v-model="formValidate.label"
              :disabled="tgaData.length == 0||$route.query.status == 'edit'"
            >
              <OptionGroup :label="tgaData.name">
                <i-Option
                  v-for="item2 in tgaData.tags"
                  :key="item2.id"
                  :value="item2.id"
                >{{item2.name}}</i-Option>
              </OptionGroup>
            </i-Select>
          </form-item>
          <form-item label="项目所在地址" prop="address" @submit.native.prevent>
            <Cascader
              :placeholder="$route.query.status == 'edit' && formValidate.address != ''? JSON.parse(this.formValidate.address).name : '省/市/区'"
              @on-change="address"
              :data="addList"
              style="width: 30%; float: left; margin-right:1%;"
              :disabled="$route.query.status == 'edit'"
            ></Cascader>
            <Input v-model="formValidate.detailedAddress" placeholder="详细地址" style="width: 69%;" />
          </form-item>
          <form-item label="商业计划书 ( 格式: PDF )" prop="bp" @submit.native.prevent>
            <Upload
              accept=".pdf"
              :action="fileconfig.fileUrl"
              :headers="fileconfig.fileHead"
              name="file"
              :show-upload-list="false"
              :on-success="bp"
            >
              <Button icon="ios-cloud-upload-outline">商业计划书</Button>
              <div v-if="formValidate.bp != ''">
                <p>{{formValidate.bp}}</p>
                <p style="color: #2868d1;">*已添加,想更换直接上传新商业计划书即可</p>
              </div>
            </Upload>
          </form-item>
          <form-item label="官网地址或APP下载地址" @submit.native.prevent>
            <Input v-model="formValidate.url" placeholder="官网地址或APP下载地址" />
          </form-item>
          <form-item label="项目LOGO ( 格式: 照片 )" @submit.native.prevent>
            <label>
              <Upload
                accept="image/*"
                :action="fileconfig.fileUrl"
                :headers="fileconfig.fileHead"
                name="file"
                :on-success="logo"
                :show-upload-list="false"
              >
                <Button icon="ios-cloud-upload-outline">项目LOGO</Button>
                <div v-if="formValidate.logo != ''">
                  <p>{{formValidate.logo}}</p>
                  <p style="color: #2868d1;">*已添加,想更换直接上传新LOGO即可</p>
                </div>
              </Upload>
            </label>
          </form-item>
        </div>
        <div v-show="active==1">
          <form-item label="去年营业额" prop="turnover" @submit.native.prevent>
            <Input
              v-model="formValidate.turnover"
              :disabled="$route.query.status == 'edit'"
              placeholder="去年营业额"
              style="width:200px"
            >
              <span slot="append">万元</span>
            </Input>
          </form-item>
          <form-item label="企业当前净利润" prop="profit" @submit.native.prevent>
            <Input
              v-model="formValidate.profit"
              :disabled="$route.query.status == 'edit'"
              placeholder="企业当前净利润"
              style="width:200px"
            >
              <span slot="append">万元</span>
            </Input>
          </form-item>
          <form-item label="当前轮次" prop="nowRound" @submit.native.prevent>
            <i-Select
              v-model.number="formValidate.nowRound"
              style="width: 100px;margin-right: 10px;"
              :disabled="$route.query.status == 'edit'"
            >
              <i-Option
                v-for="(item, index) in data.rotations"
                :key="index"
                :value="item.id"
              >{{item.name}}</i-Option>
            </i-Select>
          </form-item>
          <form-item label="当前融资金额" prop="nowMoney" @submit.native.prevent>
            <Input
              v-model="formValidate.nowMoney"
              placeholder="当前融资金额"
              style="width:200px;margin-top: -1px;margin-right: 10px;"
              :disabled="$route.query.status == 'edit'"
            >
              <span slot="append">万元</span>
            </Input>
          </form-item>
          <form-item label="当前轮次投资方占比" prop="nowProportion" @submit.native.prevent>
            <Input
              v-model="formValidate.nowProportion"
              placeholder="投资方占比"
              style="width:200px;   margin-top: -1px;"
              :disabled="$route.query.status == 'edit'"
            >
              <span slot="append">百分比 %</span>
            </Input>
          </form-item>
          <form-item label="当前融资方式" prop="way" @submit.native.prevent>
            <i-Select
              v-model.number="formValidate.way"
              style="width: 100px;margin-right: 10px;"
              :disabled="$route.query.status == 'edit'"
            >
              <i-Option
                v-for="(item, index) in data.styles"
                :key="index"
                :value="item.id"
              >{{item.name}}</i-Option>
            </i-Select>
          </form-item>
          <form-item label="最短退出年限" prop="outyear" @submit.native.prevent>
            <Input
              v-model="formValidate.outyear"
              placeholder="最短退出年限"
              style="width:200px;   margin-top: -1px;"
              :disabled="$route.query.status == 'edit'"
            >
              <span slot="append">年</span>
            </Input>
          </form-item>
          <form-item label="上轮获投" @submit.native.prevent>
            <i-Select
              v-model="formValidate.beforeRound"
              style="width: 100px;margin-right: 10px;"
              :disabled="$route.query.status == 'edit'"
            >
              <i-Option value="-1" label="无"></i-Option>
              <i-Option
                v-for="(item) in data.rotations"
                :key="item.id"
                :value="item.id"
                :label="item.name"
              ></i-Option>
            </i-Select>
          </form-item>
          <form-item
            label="上轮获投金额"
            prop="beforeMoney"
            @submit.native.prevent
            v-if="formValidate.beforeRound != -1"
          >
            <Input
              v-model="formValidate.beforeMoney"
              placeholder="上轮获投金额"
              style="width:200px;margin-top: -1px;margin-right: 10px;"
              :disabled="$route.query.status == 'edit'"
            >
              <span slot="append">万元</span>
            </Input>
          </form-item>
          <form-item
            label="上轮投资方占比"
            prop="proportion"
            @submit.native.prevent
            v-if="formValidate.beforeRound != -1"
          >
            <Input
              v-model="formValidate.proportion"
              placeholder="上轮投资方占比"
              style="width:200px;   margin-top: -1px;"
              :disabled="$route.query.status == 'edit'"
            >
              <span slot="append">百分比 %</span>
            </Input>
          </form-item>
        </div>
        <div v-show="active==2">
          <div prop="contact_name">
            <p>
              <Button @click="addMember" type="primary" icon="md-add" ghost>添加成员</Button>
            </p>
            <br />
            <ul>
              <li v-for="(item, index) in formValidate.group" :key="index">
                <p style="font-size:16px;margin-left:10px;">成员{{index+1}}</p>
                <br />
                <Input
                  prefix="ios-contact"
                  placeholder="称呼"
                  style="width: auto; margin-right:20px;"
                  v-model="item.name"
                />
                <Input v-model="item.position" placeholder="职位" style="width: auto" />
                <br />
                <Input
                  v-model="item.introduce"
                  placeholder="介绍"
                  style="width: 600px;margin-top:10px;"
                  type="textarea"
                />
                <p>
                  <Poptip confirm title="是否要删除,请确认?" @on-ok="deleteMember(index)">
                    <Button type="error" ghost style="margin-top:10px;">删除成员</Button>
                  </Poptip>
                </p>
                <br />
                <br />
              </li>
            </ul>
            <form-item prop="outyear" @submit.native.prevent></form-item>
            <br />
            <br />
            <br />
          </div>
        </div>
        <div v-show="active==3">
          <form-item label="项目联系人姓名" prop="contactName" @submit.native.prevent>
            <Input v-model="formValidate.contactName" placeholder="项目联系人姓名" />
          </form-item>
          <form-item label="项目联系号码" prop="contactPhone" @submit.native.prevent>
            <Input v-model="formValidate.contactPhone" placeholder="项目联系号码" />
          </form-item>
          <form-item label="项目联系人邮箱" @submit.native.prevent>
            <Input v-model="formValidate.contactEmail" placeholder="项目联系人邮箱" />
          </form-item>
        </div>
        <form-item>
          <Button type="primary" style="margin-right:20px;" v-if="active != 0" @click="active--">上一步</Button>
          <Button type="primary" v-if="active != 3" @click="handleSubmit('formValidate')">下一步</Button>
          <Button
            type="primary"
            v-if="active == 3"
            @click="handleSubmit('formValidate')"
          >{{$route.query.status == 'edit' ? '修改项目' : '创建项目'}}</Button>
        </form-item>
      </i-form>
    </div>
  </div>
</template>
<script>
import { getRealmTag } from "../../../api/public";
import { project, getProjectUser, projectEdit } from "../../../api/project";
import { mapActions, mapState } from "vuex";
import {
  messages,
  fileUploadUrl,
  validateNub,
  validatePhone
} from "../../../utils";
import { areasList } from "../../../areas-list";
import { getPassAuditing } from "../../../api/user";
import {
  Form,
  Radio,
  FormItem,
  Step,
  Select,
  OptionGroup,
  Cascader,
  Upload,
  Button,
  Input,
  Poptip,
  RadioGroup,
  Option,
  Steps
} from "iview";
export default {
  components: {
    [Form.name]: Form,
    [Radio.name]: Radio,
    [FormItem.name]: FormItem,
    [Step.name]: Step,
    [Select.name]: Select,
    [OptionGroup.name]: OptionGroup,
    [Cascader.name]: Cascader,
    [Upload.name]: Upload,
    [Button.name]: Button,
    [Option.name]: Option,
    [Poptip.name]: Poptip,
    [RadioGroup.name]: RadioGroup,
    [Input.name]: Input,
    [Steps.name]: Steps
  },
  computed: {
    ...mapState("user", ["userInfo"])
  },
  data() {
    return {
      // 文件上传配置
      fileconfig: {
        fileUrl: fileUploadUrl,
        fileHead: {
          token: document.cookie.match(
            new RegExp("(^| )" + "token" + "=([^;]*)(;|$)")
          )[2]
        }
      },
      //用户角色权限信息
      userAuth: "",
      //第几步 0基础 1项目信息 2团队 3项目联系人
      active: 0,
      //页面进入获取数据 融资列表 领域列表
      data: [],
      // 根据领域获取到的标签数据
      tgaData: [],
      //省市区列表data
      addList: areasList,
      // 提交信息
      formValidate: {
        name: "",
        logo: "",
        type: "",
        companyId: "",
        address: "",
        detailedAddress: "",
        territory: "",
        label: "",
        nowRound: [],
        nowMoney: "",
        bp: "",
        url: "",
        introduce: "",
        turnover: "",
        profit: "",
        beforeRound: "-1",
        beforeMoney: "",
        proportion: "",
        outyear: "",
        group: [
          {
            name: "",
            position: "",
            introduce: ""
          }
        ],
        contactName: "",
        contactPhone: "",
        contactEmail: "",
        way: ""
      },
      //表单验证
      ruleValidate: {
        type: [
          { required: true, message: "项目类型,不能为空", trigger: "change" }
        ],
        companyId: [
          {
            required: true,
            message: "企业,不能为空",
            trigger: "blur",
            type: "number"
          }
        ],
        name: [{ required: true, message: "项目名,不能为空", trigger: "blur" }],
        address: [
          {
            required: true,
            message: "省/市/区,不能为空",
            trigger: "change"
          }
        ],
        territory: [
          {
            required: true,
            message: "领域,不能为空",
            trigger: "change",
            type: "number"
          }
        ],
        label: [
          {
            required: true,
            type: "array",
            message: "最少选择一个标签,不能为空",
            trigger: "change"
          }
        ],
        nowRound: [
          {
            required: true,
            message: "当前融资轮次,不能为空",
            trigger: "change",
            type: "number"
          }
        ],
        nowMoney: [
          {
            required: true,
            message: "当前融资金额,必须为数字,最多两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ],
        nowProportion: [
          {
            required: true,
            message: "当前融资投资方占比,必须为数字,最多两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ],
        bp: [
          { required: true, message: "商业计划书,不能为空", trigger: "change" }
        ],
        introduce: [
          { required: true, message: "项目介绍,不能为空", trigger: "blur" }
        ],
        turnover: [
          {
            required: true,
            message: "去年营业额,必须为数字,最多两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ],
        profit: [
          {
            required: true,
            message: "企业当前净利润,必须为数字,最多两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ],
        outyear: [
          {
            required: true,
            message: "最短退出年限,必须为数字,最多两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ],
        contactName: [
          {
            required: true,
            message: "项目联系人姓名,不能为空",
            trigger: "blur"
          }
        ],
        way: [
          {
            required: true,
            message: "融资方式,不能为空",
            trigger: "change",
            type: "number"
          }
        ],
        contactPhone: [
          {
            required: true,
            message: "项目联系方式不正确,请从新输入",
            trigger: "blur",
            validator: validatePhone
          }
        ],
        beforeMoney: [
          {
            message: "上轮获投金额,必须为数字,最多两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ],
        proportion: [
          {
            message: "上轮投资方占比,必须为数字,最多两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ]
      }
    };
  },
  methods: {
    ...mapActions("user", ["getUserInfo"]),
    //选择了所属领域后把对应标签返回
    fieldChange(value) {
      for (const item of this.data.realms) {
        if (item.id == value) {
          this.formValidate.label = [];
          return (this.tgaData = item);
        }
      }
    },
    /**
     * @function 省市区监听
     */
    address(value, selectedData) {
      let selectedDatas = selectedData.pop();
      this.formValidate.address = JSON.stringify({
        name: selectedDatas.__label,
        value: selectedDatas.__value
      });
    },
    //上传BP
    bp(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.bp = res.data.url;
      }
    },
    //上传lOGO
    logo(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.logo = res.data.url;
      }
    },
    //添加成员
    addMember() {
      this.formValidate.group.push({
        name: "",
        position: "",
        introduce: ""
      });
    },
    // 删除成员
    deleteMember(index) {
      this.formValidate.group.splice(index, 1);
    },
    handleSubmit(name) {
      let _this = this;
      switch (_this.active) {
        case 0:
          var fieldsToValidate = [
            "name",
            "type",
            "introduce",
            "territory",
            "address",
            "label",
            "bp"
          ];
          //第一步如果是选择想政府那么多一个选择公司字段验证
          if (_this.formValidate.type == "2") {
            fieldsToValidate.push("companyId");
          }
          Promise.all(
            fieldsToValidate.map(item => {
              let p = new Promise(function(resolve, reject) {
                _this.$refs["formValidate"].validateField(item, error => {
                  resolve(error);
                });
              });
              return p;
            })
          ).then(data => {
            if (data.join("").length == 0) {
              _this.active++;
            } else {
              messages.error(`有信息必须填写，请完善填写的信息`);
            }
          });
          break;
        case 1:
          var fieldsToValidate = [
            "turnover",
            "profit",
            "nowRound",
            "nowMoney",
            "nowProportion",
            "way",
            "outyear"
          ];
          //第二部 如果有上轮获投 那么上轮占比和上轮金额需要验证
          if (this.formValidate.beforeRound != "-1") {
            fieldsToValidate.push("beforeMoney");
            fieldsToValidate.push("proportion");
          }
          Promise.all(
            fieldsToValidate.map(item => {
              let p = new Promise(function(resolve, reject) {
                _this.$refs["formValidate"].validateField(item, error => {
                  resolve(error);
                });
              });
              return p;
            })
          ).then(data => {
            if (data.join("").length == 0) {
              _this.active++;
            } else {
              messages.error(`有信息必须填写，请完善填写的信息`);
            }
          });
          break;
        case 2:
          if (this.formValidate.group.length > 0) {
            for (const item of this.formValidate.group) {
              if (item.name == "" || item.position == "") {
                return messages.error(
                  "成员名字和岗位信息必须填写,如果成员信息没有请删除成员"
                );
              }
            }
            _this.active++;
          } else {
            messages.error("团队成员必须要有一位");
          }
          break;
        case 3:
          if (this.$route.query.status == "edit") {
            this.$refs[name].validate(
              function(valid) {
                if (valid) {
                  projectEdit(this.formValidate).then(res => {
                    if (res.data.status == "SUCCESS") {
                      messages.success(
                        "项目修改成功，项目重新进入审核，将在1-7日内完成审核"
                      );
                      this.$router.push("/user/project-index");
                    }
                  });
                }
              }.bind(this)
            );
          } else {
            this.$refs[name].validate(
              function(valid) {
                if (valid) {
                  project(this.formValidate).then(res => {
                    if (res.data.status == "SUCCESS") {
                      this.getUserInfo();
                      messages.success("项目创建成功，将在1-7日内完成审核");
                      this.$router.push("/user/project-index");
                    }
                  });
                }
              }.bind(this)
            );
          }
          break;
      }
    }
  },
  created() {
    if (this.$route.query.status != "edit" && this.userInfo.project.nowCount >= this.userInfo.project.maxCount) {
      messages.error("您的项目发布已达上限,发布更多项目请了解VIP权益");
      this.$router.push("/user/project-index");
      return;
    }
    getPassAuditing().then(res => {
      this.userAuth = res.data.data;
      // 如果用户的角色是0
      if (this.userAuth.auditingType == 0) {
        messages.error("您还没有认证,请先完成认证才可以发布项目");
        this.$router.push("/user/certify-index");
      }
      this.formValidate.type = this.userAuth.auditingType.toString();
      getRealmTag().then(res => {
        this.data = res.data.data;
        //如果页面是修改
        if (this.$route.query.status == "edit") {
          getProjectUser({ id: this.$route.query.id }).then(res => {
            this.formValidate = res.data.data;
            this.formValidate.label = eval(`(${this.formValidate.label})`);
            this.formValidate.territory = parseInt(this.formValidate.territory);
            this.formValidate.nowRound = parseInt(this.formValidate.nowRound);
            this.formValidate.way = parseInt(this.formValidate.way);
            this.formValidate.beforeRound =
              this.formValidate.beforeRound == ""
                ? "-1"
                : parseInt(this.formValidate.beforeRound);
            console.log(this.formValidate.beforeRound);
            for (const item of this.data.realms) {
              if (item.id == this.formValidate.territory) {
                return (this.tgaData = item);
              }
            }
          });
        }
      });
    });
  }
};
</script>
